import React,{useState} from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import AccountCircle from '@material-ui/icons/AccountCircle';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import CardGiftcardIcon from '@material-ui/icons/CardGiftcard';
import MoreIcon from '@material-ui/icons/MoreVert';
import {connect} from 'react-redux';
import Button from '@material-ui/core/Button';
import * as actions from '../../../store/actions/index';
// import Modal from '../../Modal/Modal';

const useStyles = makeStyles(theme => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    color:'white',
    textTransform:"capitalize",
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '20ch',
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
}));

const Header=(props)=> {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleProfileMenuOpen = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = event => {
    setMobileMoreAnchorEl(event.currentTarget);
  };
  
  const cartHandler=()=>{
    props.detailsClosed()
    props.history.push('/cart')
  }
  const orderHandler=()=>{
    props.detailsClosed()
    // props.ordersCountInit()
    props.history.push('/orders')
  }
const loginHandler=()=>{
  return(
   props.history.push('/auth')
  )
}
const addProductHandler=()=>{
  props.history.push('/add-items')
}
// console.log(props)
let yourOrders=null
let yourOrdersDesktop=null
if(props.isAuthenticated){
  yourOrders=(
    <MenuItem onClick={orderHandler}>
    <IconButton aria-label="show 11 new notifications" color="inherit">
      <Badge badgeContent={props.ordersCount} color="secondary">
        <CardGiftcardIcon />
      </Badge>
    </IconButton>
    <p>Your Orders</p>
  </MenuItem>
  )
  yourOrdersDesktop=(
    <IconButton aria-label="show 17 new notifications" color="inherit" onClick={orderHandler}>
    <Badge badgeContent={props.ordersCount} color="secondary">
      <CardGiftcardIcon />
    </Badge>
  </IconButton>
  )
}
const logoutHandler=()=>{
  props.history.push('/logout')
}
 let signInStatus=<MenuItem onClick={loginHandler}>Login</MenuItem>
 let status='login'
 if(props.isAuthenticated){
   signInStatus= <div><MenuItem onClick={logoutHandler}>Logout</MenuItem>
                <MenuItem onClick={handleMenuClose}>Profile</MenuItem>
                <MenuItem onClick={addProductHandler}>Add Product</MenuItem></div>
   status='Profile';
 }
 
  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
     {signInStatus}
    </Menu>
  );

  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem onClick={cartHandler}>
        <IconButton aria-label="show 4 new mails" color="inherit">
          <Badge badgeContent={props.noOfcartItems} color="secondary">
            <ShoppingCartIcon />
          </Badge>
        </IconButton>
        <p>Cart</p>
      </MenuItem>
      {yourOrders}
      <MenuItem onClick={handleProfileMenuOpen}>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>{status}</p>
      </MenuItem>
    </Menu>
  );
  const homepageHandler=()=>{
  props.history.push('/')
  }
// console.log(props.ordersCount)
  return (
    <div className={classes.grow}>
      <AppBar position="fixed" style={{height:'66px',backgroundColor:'#00bcd4'}}>
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="open drawer"
            onClick={()=>{console.log('jhgfgh')}}
          >
            <MenuIcon />
          </IconButton>
          <Button onClick={homepageHandler}>
           <Typography className={classes.title} variant="h6" noWrap>
            E-Commerce
          </Typography>
          </Button>
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'search' }}
            />
          </div>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            <IconButton aria-label="show 4 new mails" color="inherit" onClick={cartHandler}>
              <Badge badgeContent={props.noOfcartItems} color="secondary">
                <ShoppingCartIcon />
              </Badge>
            </IconButton>
            {yourOrdersDesktop}
            <IconButton
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
      {renderMenu}
    </div>
  );
}

const mapPropsToState=(state)=>{
  return{
    noOfcartItems:state.cart.items.length,
    ordersCount:state.UI.ordersCount,
    isAuthenticated:state.auth.isAuthenticated,
  }
}
const mapDispatchToProps=(dispatch)=>{
  return{
    // ordersCountInit:()=>dispatch({type:actionType.ORDERS_COUNT_INIT}),
    detailsClosed:()=>dispatch(actions.detailsClosed()),
  }
}

export default connect(mapPropsToState,mapDispatchToProps)(Header);